.applicationContainer {
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: 12vh 1fr;
  grid-gap: 10px 0;
  background: #ededed 0% 0% no-repeat padding-box;
  min-height: 100vh;
}

.applicationHeader {
  display: flex;
  align-items: center;
  background: transparent
    linear-gradient(
      180deg,
      #fcfcfc 0%,
      #f4f4f4 38%,
      #ebebeb 82%,
      #f0f0f0 93%,
      #f4f4f4 97%,
      #fcfcfc 100%
    )
    0% 0% no-repeat padding-box;
}

.applicationLogo {
  width: auto;
  height: 100%;
}

.caseListWrapper {
  /* width: 85%; */
  width: 70%;
  padding-left: 15px;
}

.modificationsWrapper {
  display: grid;
  grid-template-columns: 25% 3% 65%;
  margin-top: 30px;
}

