.caseCardLayoutWrapper {
  width: 95%;
  min-width: 1200px;
  margin-top: 25px;
}

.customAreaWrapper {
  display: grid;
  grid-template-columns: 40% 40%;
  grid-gap: 20px 20px;
  grid-template-rows: 1fr 1fr;
  margin-top: 40px;
}

.areaFlexWrapper {
  display: flex;
  flex-direction: column;
}
