.caseCardWrapper {
  display: flex;
  align-items: center;
  flex-direction: row;
}

.caseStatusText {
  font-size: 17px;
  color: black;
  font-weight: bold;
  padding-left: 5px;
  margin: 0;
}

.caseStatusPendingIcon {
  color: white;
  transform: scale(0.7) translate(2px, 1px);
}
.caseStatusActiveIcon {
  color: white;
  transform: scale(0.7) translate(2px, 2px);
}
.caseStatusClosedIcon {
  color: white;
  transform: scale(0.7);
}
.caseStatusIcon {
  width: 20px;
  height: 20px;
  text-align: center;
  border-radius: 99px;
}
