.caseCard {
  display: grid;
  min-height: 25vh;
  grid-template-rows: minmax(300px, auto);
  grid-template-columns: 60% 20% 20%;
  margin-top: 15px;
  background-color: #f0f0f0;
  border-radius: 5px;
  border: solid #00000029 2px;
}

.sectionOne {
  display: grid;
  grid-template-rows: 25% 2fr 30% 17%;
  border-right: solid 2px #00000029;
  background-color: #fdfdfd;
  min-width: 0;
  padding: 0 10px 0 10px;
}

.sectionTwo {
  display: grid;
  grid-template-rows: 15% auto auto;
  border-right: solid 2px #00000029;
}

.sectionThree {
}

.bookmarkSection {
  width: 5%;
}

.cardHeaderWrapper {
  display: flex;
  width: 95%;
  flex-direction: column;
}

.departmentContainer {
  border-bottom: solid #00000029 2px;
  padding: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.departmentText {
  width: 100%;
  background-color: #a7a7a7;
  padding: 5px 0;
  text-align: center;
  margin: 0;
  text-transform: uppercase;
  color: white;
  border-radius: 6px;
}

.cardHeader {
  display: flex;
  border-bottom: solid #00000029 2px;
  /* padding-right: 10px; */
}

.cardHeading {
  width: 100%;
  align-items: center;
  display: inline-flex;
  justify-content: space-between;
}

.cardHeading > h1 {
  font-size: 26px;
  margin: 0;
}

.cardHeading > h2 {
  margin: 0;
  font-size: 26px;
  text-align: right;
}

.cardSubHeading > h3 {
  font-size: 26px;
  margin: 0;
  color: #a5a5a5;
  text-align: left;
  font-size: 20px;
}

.cardSubHeading > h2 {
  margin: 0;
  color: #a5a5a5;
  font-size: 20px;
  text-align: right;
}

.cardSubHeading {
  width: 100%;
  align-items: center;
  display: inline-flex;
  justify-content: space-between;
  margin-top: 10px;
}

.caseAreaOne {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  margin-top: 10px;
  min-width: 0;
  border-bottom: solid #00000029 2px;
}

.caseAreaTwo {
  display: flex;
  min-width: 0;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  margin-top: 10px;
  border-bottom: solid #00000029 2px;
}

@media only screen and (max-width: 1366px) and (min-width: 1025px) {
  .caseAreaFooter {
    grid-template-columns: 35% 20% 25% 20%;
    grid-gap: 0 5px;
  }
}

@media (min-width: 1370px) {
  .caseAreaFooter {
    grid-template-columns: 35% 20% 25% 20%;
    grid-gap: 5px 0;
  }
}

.caseAreaFooter {
  display: grid;
}

