.caseAreaThreeFields {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 0 10px 0 10px;
}

.caseAreaThreeFields > div {
  flex: 50%;
}

.caseAreaThreeTimeline {
  border-top: solid #00000029 2px;
  display: grid;
  grid-template-columns: 4fr 1fr 2fr;
  grid-gap: 5px;
  padding-left: 5px;
}
.caseAreaThreeDesc {
  border-top: solid #00000029 2px;
}

.areaTwoWrapper {
}

.footerInfoStyles {
  color: #a5a5a5;
  font-size: 14px;
  padding-left: 5px;
  text-transform: uppercase;
}

.caseFooterWrapper {
  display: flex;
  align-items: center;
}

.caseFooterIcon {
  color: #0078d4;
  transform: rotate(45deg);
}
.caseFooterHighlightedText {
  color: #000000;
  font-size: 16px;
  padding-left: 5px;
}
