.timelineDotContainer {
    border: 1px solid transparent;
    border-radius: 50%;
    height: 20px;
    width: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: auto;
    position: relative;
    z-index: 2;
    cursor: pointer;
}

.timelineDotContainer.enabled {
    border-color: #0078D4;
}

.timelineDot {
    height: 10px;
    width: 10px;
    background-color: #B8B8B8B8;
    border-radius: 50%;
}

.timelineDot.enabled {
    background-color: #0078D4;
}

.timelineVerticalLine {
    position: absolute;
    margin-left: auto;
    margin-right: auto;
    height: 30px;
    background-color: rgba(184, 184, 184, 0.4);
    width: 2px;
    top: 15px;
    z-index: -1;
}

.timelineVerticalLineReverse {
    bottom: 15px;
    top: auto;
}

.displayNone {
    display: none;
}

.timelineItems {
    display: flex;
    align-items: center;
    margin: 0;
}